import Noticia from '../components/Noticia'

const Noticias = (props) => {

    console.log(props)

    return (
        <div className="row">
            {props.noticias.map(noticia => (
                <Noticia
                    key={noticia.publishedAt}
                    noticia={noticia}
                />
            ))}
        </div>
    );
};

export default Noticias;
